//ready to use
@mixin media {
  display: inline-block;
  line-height: 0;
  overflow: hidden;
  position: relative; // need to position .marker properly
  transition: opacity 0.5s;
  vertical-align: text-top;

  &:hover {
    img {
      opacity: 0.56;
    }
  }

  img {
    max-width: 100%;
    min-height: 20px;
    transition: opacity 0.25s;
  }
}
